.orders {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.orders__success{
  margin-top: 150px;
  color: green;
}

.orders__continue{
    background: #f0c14b;
    border-radius: 2px;
    width: 200px;
    height: 30px;
    border: 1px solid;
    text-decoration-color: black;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}
